@import url(https://fonts.googleapis.com/css2?family=Noto+Sans+JP:wght@300);
@import url(https://fonts.googleapis.com/css2?family=Merriweather:wght@700);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

:root {
  --bg-color: #ffffff;
  --pane-color: #f6f6f6;
  --fg-color: #002a52;
  --fg-color-2: #a8a8a8;
}

@media (prefers-color-scheme: dark) {
  :root {
    --bg-color: #181818;
    --pane-color: #121212;
    --fg-color: #c4c4c4;
    --fg-color-2: #353535;
  }
  .App {
    background-image: url(/ds-course-reviews/static/media/tiledark.2a3d822b.png);
  }
}

[data-theme="dark"] {
  --bg-color: #181818;
  --pane-color: #121212;
  --fg-color: #b9acc9;
  --fg-color-2: #353535;
}
[data-theme="dark"] .App {
  background-image: url(/ds-course-reviews/static/media/tiledark.2a3d822b.png);
}

.App {
  color: #002a52;
  color: var(--fg-color);
  background-color: #ffffff;
  background-color: var(--bg-color);
  background-image: url(/ds-course-reviews/static/media/tile.c4475155.png);
  background-position: 0px 0px;
  background-attachment: fixed;
  background-repeat: repeat;
  font-family: 'Noto Sans JP', sans-serif;
  min-height: 100vh;
}

h1 {
  font-family: 'Merriweather', serif;
  font-size: 30pt;
  text-align: center;
}

h2 {
  font-family: 'Merriweather';
  font-size: 150%;
}

center#loading {
  margin-top: 400px;
}

.review {
  max-width: 500px;
  margin: auto;
  padding-top: 20px;
  padding-bottom: 20px;
  border-bottom: 1px solid darkgray;
}

.column {
  background-color: inherit;
  max-width: 900px;
  min-height: 100vh;
  margin: auto;
  padding: 10px;
}

.selector {
  color: #002a52;
  color: var(--fg-color);
  background-color: #f6f6f6;
  background-color: var(--pane-color);
  border: 1px solid #a8a8a8;
  border: 1px solid var(--fg-color-2);
  box-shadow: none;
  font-family: 'Noto Sans JP', sans-serif;
  font-size: large;
  min-width: 400px;
  padding: 10px;
  margin: 10px;
  display: inline-block;
}

textarea::-webkit-input-placeholder {
  color: #a8a8a8;
  color: var(--fg-color-2);
}

textarea:-ms-input-placeholder {
  color: #a8a8a8;
  color: var(--fg-color-2);
}

textarea::-ms-input-placeholder {
  color: #a8a8a8;
  color: var(--fg-color-2);
}

textarea::placeholder {
  color: #a8a8a8;
  color: var(--fg-color-2);
}

button {
  font-family: inherit;
  font-size: large;
  padding: 10px;
  margin: 10px;
  border: 2px solid #002a52;
  border: 2px solid var(--fg-color);
  background-color: #ffffff;
  background-color: var(--bg-color);
  color: #002a52;
  color: var(--fg-color);
}
button:hover {
  cursor: pointer;
  transition: 0.3s;
  background-color: #002a52;
  background-color: var(--fg-color);
  color: #ffffff;
  color: var(--bg-color);
}

.review-writer .selector {
  display:block;
}
.review-writer label {
  display: inline-block;
  margin-top: 30px;
  margin-left: 10px;
}

#darkmodeButton {
  margin: 15px;
  position: fixed;
}
#darkmodeButton:hover {
  cursor: pointer;
}
